// _reset.scss
html {
    font-size: percentage($base / 16);
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

html,
body {
    width: 100%;
    overflow-x: hidden;
}

body {
    font: 1rem/#{$leading} $body-font,
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    'Open Sans',
    'Helvetica Neue',
    sans-serif;
    background-color: #fff;
}

ol,
ul {
    list-style: none;
}

img,
video {
    max-width: 100%;
    height: auto;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

td,
th {
    padding: 0;
    text-align: left;
}

%focus {
    outline: 3px dotted $veryDarkBlue;
    outline-offset: 3px;
}

a {
    display: inline-block;
    text-decoration: none;
    transition: none;

    &:focus {
        @extend %focus;
    }
}

input,
select,
textarea {
    appearance: none;
    border-radius: none;
}

fieldset,
input {
    min-height: rem(48);
    border: transparent;
    padding: $space-m $space-l;
    border-radius: 4px;
    font-size: inherit;
}

input[type=submit],
input[type=button] {
    font-weight: 600;
}