// _globals.scss

$ratio: 1.5;
$leading: $ratio;
$spacing: $ratio+rem;
$base: 18;


// Colours

// Theme Primary
$red: hsl(0, 100%, 68%);

// Neutral/Accents/Tints
$veryDarkBlue: hsl(230, 29%, 20%);
$darkGrayishBlue: hsl(230, 11%, 40%);
$grayishBlue: hsl(231, 7%, 65%);
$lightGrayishBlue: hsl(207, 33%, 95%);

// B&W Greys
$white: #fff;
$black: #000;
$gray-90: darken($white, 90);
$gray-80: darken($white, 80);
$gray-70: darken($white, 70);
$gray-30: darken($white, 30);
$gray-20: darken($white, 20);
$gray-10: darken($white, 10);
$gray-5: darken($white, 5);

// Fonts
$display-font: "Barlow Condensed";
$body-font: "Barlow";

// Weights
$thin: 100;
$extra-light: 200;
$light: 300;
$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;
$black-9: 900;

// Spacing
$space-s: 0.5rem;
$space-m: 0.75rem;
$space-l: 1rem;
$space-xl: 1.5rem;
$space-extra: 3rem;
$space-super: 6rem;

// Container
$max-width: 1112;